import { createStore, applyMiddleware } from "redux";
import { composeWithDevTools } from "redux-devtools-extension";
import CQAPI from "./api";
import { combineReducers } from "redux";
import { createBrowserHistory } from "history";

export const history = createBrowserHistory();

function getLocalData(type) {
	const localUser = localStorage.getItem("user");
	return localUser ? JSON.parse(localStorage.getItem("user"))[type] : false;
}
function getLocalReport() {
	const report = localStorage.getItem("report");
	return report ? JSON.parse(localStorage.getItem("report")) : {};
}

const initialAuthState = {
	signup: false,
	user: getLocalData("user"),
	tokens: getLocalData("tokens"),
	error: [],
	loginMessage: "",
};
const initialAssessmentState = {
	showAssessmentCheckModal: false,
	report: getLocalReport(),
	responses: null,
};
const initialErrorState = {
	fullRegistration: null,
};

const authReducer = (state = initialAuthState, action) => {
	if (action.type === "auth/setUser") {
		return Object.assign({}, state, {
			user: action.payload.user,
			tokens: action.payload.tokens,
		});
	}
	if (action.type === "auth/setAccessToken") {
		return Object.assign({}, state, {
			user: state.user,
			tokens: action.payload,
		});
	}
	if (action.type === "auth/setError") {
		console.error(action.payload);
		return Object.assign({}, state, { error: [action.payload] });
	}
	if (action.type === "auth/clearError") {
		return Object.assign({}, state, { error: [] });
	}
	if (action.type === "auth/removeUser") {
		localStorage.removeItem("user");
		localStorage.removeItem("report");
		return Object.assign({}, state, { user: false, tokens: false });
	}
	if (action.type === "auth/loginMessage") {
		return Object.assign({}, state, {
			loginMessage: action.payload.loginMessage,
		});
	}
	return state;
};

const assessmentReducer = (state = initialAssessmentState, action) => {
	if (action.type === "assessment/completed") {
		return Object.assign({}, state.assessment, {
			responses: action.payload,
		});
	}
	if (action.type === "assessment/reportResponse") {
		return Object.assign({}, state, { report: action.payload });
	}
	if (action.type === "closeAssessmentCheckModal") {
		return Object.assign({}, state, { showAssessmentCheckModal: false });
	}
	if (action.type === "openAssessmentCheckModal") {
		return Object.assign({}, state, { showAssessmentCheckModal: true });
	}
	return state;
};

const errorReducer = (state = initialErrorState, action) => {
	if (action.type === "fullRegistration/error") {
		return Object.assign({}, state.fullRegistration, {
			fullRegistrationError: action.payload,
		});
	}
	return state;
};

const fetchTodosMiddleware = (store) => (next) => (action) => {
	if (action.type === "auth/findUser") {
		// Make an API call to fetch user from the server
		CQAPI.findUser(action.payload.data)
			.then((res) => {
				store.dispatch({ type: "auth/setUser", payload: res });
				action.payload.history.push("/verify-phone");
			})
			.catch((err) =>
				store.dispatch({ type: "auth/setError", payload: err.message })
			);
	}

	if (action.type === "auth/getNewAccessToken") {
		const state = store.getState();
		CQAPI.getNewAccessToken(state.auth.tokens.refresh.token)
			.then((resp) => {
				const localUser = JSON.parse(localStorage.getItem("user"));
				localUser.tokens = resp;
				localStorage.setItem("user", JSON.stringify(localUser));
				store.dispatch({ type: "auth/setAccessToken", payload: resp });
			})
			.catch((err) =>
				store.dispatch({ type: "auth/setError", payload: err.message })
			);
	}

	if (action.type === "auth/signUp") {
		// sign up new user
		CQAPI.signUp(action.payload.user)
			.then((res) => {
				store.dispatch({ type: "auth/setUser", payload: res });
				action.payload.history.push("/verify-phone");
			})
			.catch((err) =>
				store.dispatch({ type: "auth/setError", payload: err.message })
			);
	}

	if (action.type === "auth/signIn") {
		// login in user
		CQAPI.signIn(action.payload.credentials)
			.then((res) => {
				store.dispatch({ type: "auth/setUser", payload: res });
				store.dispatch({ type: "assessment/submit" });
				action.payload.history.push("/report");
			})
			.catch((err) =>
				store.dispatch({ type: "auth/setError", payload: err.message })
			);
	}

	if (action.type === "assessment/submit") {
		const state = store.getState();
		CQAPI.submitResponse(
			state.assessment.responses,
			state.auth.tokens.access.token
		).then((resp) =>
			store.dispatch({ type: "assessment/reportResponse", payload: resp })
		);
	}
	return next(action);
};

const composedEnhancer = composeWithDevTools(
	applyMiddleware(fetchTodosMiddleware)
);
// applyMiddleware(fetchTodosMiddleware)

const createRootReducer = () =>
	combineReducers({
		auth: authReducer,
		assessment: assessmentReducer,
		errors: errorReducer,
	});

const store = createStore(createRootReducer(), {}, composedEnhancer);

export default store;
