import React, { useEffect, useState } from "react";
import { Alert, Form, Button } from "react-bootstrap";
import { connect } from "react-redux";
import CQAPI from "../api";

const CreateAccountForm = (props) => {
	const [password, setPassword] = useState({
		value: "",
		error: {},
		isFocused: false,
	});
	const [userName, setUserName] = useState({
		value: "",
		error: {},
		isFocused: false,
	});
	const [mobile, setMobile] = useState({
		value: "",
		error: "",
	});
	const handleUserNameChange = (e) => {
		const value = e.target.value;
		if (password.isFocused) {
			setPassword({ ...password, isFocused: false });
		}
		setUserName({
			...userName,
			value: value,
			error: validateUserName(value),
			isFocused: true,
		});
	};
	const validateUserName = (value) => {
		return {
			strLength: value.length >= 8 && value.length <= 30,
			character: /[a-z]/.test(value),
			number: /[0-9]/.test(value),
			specialCharacters: !/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(
				value
			),
		};
	};
	const handlePasswordChange = (e) => {
		const value = e.target.value;
		if (userName.isFocused) {
			setUserName({ ...userName, isFocused: false });
		}
		setPassword({
			...password,
			value: value,
			error: validatePassword(value),
			isFocused: true,
		});
	};
	const validatePassword = (value) => {
		return {
			strLength: value.length >= 8,
			character: /[a-z A-Z]/.test(value),
			number: /[0-9]/.test(value),
		};
	};
	const handleMobileChange = (e) => {
		const value = e.target.value;
		if (userName.isFocused) {
			setUserName({ ...userName, isFocused: false });
		}
		if (password.isFocused) {
			setPassword({ ...password, isFocused: false });
		}
		setMobile({ value, error: validateMobile(value) });
	};
	const validateMobile = (value) => {
		let error = "";
		if (!value) {
			error = "mobile number is required";
		} else if (!/[0-9]{10,10}/.test(value)) {
			error = "Please provide valid mobile number";
		}
		return error;
	};
	const handleSubmit = (event) => {
		event.preventDefault();
		const mobileErrors = validateMobile(mobile.value);
		if (mobileErrors) {
			setMobile({ ...mobile, error: mobileErrors });
			return;
		}
		const userNameErrors = validateUserName(userName.value);
		if (Object.values(userNameErrors).includes(false)) {
			setUserName({
				...userName,
				isFocused: true,
				error: userNameErrors,
			});
			return;
		} else if (userName.isFocused) {
			setUserName({ ...userName, isFocused: false });
		}
		const passwordErrors = validatePassword(password.value);
		if (Object.values(passwordErrors).includes(false)) {
			setPassword({
				...password,
				isFocused: true,
				error: passwordErrors,
			});
			return;
		}
		const data = {
			userName: userName.value.toLowerCase(),
			password: password.value,
			mobile: mobile.value,
		};
		props.onSubmit(data);
	};
	// const formic = useFormik({
	// 	initialValues: {
	// 		// firstName: '',
	// 		// lastName: '',
	// 		email: "",
	// 		userName: "",
	// 		password: "",
	// 	},
	// 	onSubmit: (values) => {
	// 		values.userName = userName.value.toLowerCase();
	// 		values.password = password.value;
	// 		values.email = email.value;
	// 		if (email.error) {
	// 			return;
	// 		}
	// 		if (Object.values(userName.error).includes(false)) {
	// 			setUserName({ ...userName, isFocused: true });
	// 			return;
	// 		} else if (userName.isFocused) {
	// 			setUserName({ ...userName, isFocused: false });
	// 		}
	// 		if (Object.values(password.error).includes(false)) {
	// 			setPassword({ ...password, isFocused: true });
	// 			return;
	// 		}
	// 		props.onSubmit(values);
	// 	},
	// 	validate: (values) => {
	// 		let errors = {};
	// 		if (!values.firstName) {
	// 		    errors.firstName = 'First name is required'
	// 		} else if (/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(values.firstName)) {
	// 		    errors.firstName = 'Do not enter Special Character'
	// 		}
	// 		if (!values.lastName) {
	// 			errors.lastName = "Last name is required";
	// 		} else if (
	// 			/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(values.lastName)
	// 		) {
	// 			errors.lastName = "Do not enter Special Character";
	// 		}
	// 		if (!values.email) {
	// 			errors.email = "email is required";
	// 		} else if (
	// 			!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
	// 		) {
	// 			errors.email = "Please provide valid email address";
	// 		}
	// 		return errors;
	// 	},
	// });
	return (
		<div className="col-md-12">
			<Form
				autoComplete="new-password"
				noValidate
				onSubmit={handleSubmit}
			>
				<div className="row">
					<div className="col-md-6 leftBdr">
						<Form.Group controlId="firstName">
							<div className="row">
								<div className="col-md-4">
									<label>First Name</label>
								</div>
								<div className="col-md-8">
									<Form.Control
										autoComplete="off"
										type="text"
										minLength={1}
										placeholder="First Name"
										value={props.user.firstName}
										disabled
									/>
								</div>
							</div>
						</Form.Group>

						<Form.Group controlId="lastName">
							<div className="row">
								<label className="col-md-4">Last Name</label>
								<div className="col-md-8">
									<Form.Control
										autoComplete="on"
										className="col-md-8"
										value={props.user.lastName}
										minLength={1}
										type="text"
										placeholder="Last Name"
										disabled
									/>
								</div>
							</div>
						</Form.Group>
						<Form.Group controlId="email">
							<div className="row">
								<label className="col-md-4">Email</label>
								<div className="col-md-8">
									<Form.Control
										autoComplete="on"
										value={
											props.user.email[0]?.address ||
											"email"
										}
										minLength={1}
										type="text"
										placeholder="email"
										disabled
									/>
								</div>
							</div>
						</Form.Group>
					</div>
					<div className="col-md-6">
						<Form.Group controlId="mobile">
							<Form.Control
								autoComplete="new-password"
								value={mobile.value}
								// onBlur={formic.handleBlur}
								onChange={handleMobileChange}
								type="text"
								placeholder="Mobile Number"
								maxLength="10"
								minLength="10"
								required
							/>
							{mobile.error ? (
								<Alert variant="danger errorMsg">
									{mobile.error}
								</Alert>
							) : null}
							{/* <Form.Control.Feedback type="invalid">{errors.mobile}</Form.Control.Feedback> */}
						</Form.Group>
						<Form.Group controlId="userName">
							{/* <div className="toolTip"> */}
							<Form.Control
								autoComplete="new-password"
								value={userName.value}
								// onBlur={formic.handleBlur}
								onChange={handleUserNameChange}
								type="text"
								placeholder="Create a username"
								required
							/>
							{userName.isFocused ? (
								<div className="validation">
									<div className="validator">
										<i
											className={
												userName.error.strLength
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: userName.error.strLength
													? "green"
													: "red",
											}}
										>
											8-30 Characters
										</p>
									</div>
									<div className="validator">
										<i
											className={
												userName.error.number
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: userName.error.number
													? "green"
													: "red",
											}}
										>
											At least one number
										</p>
									</div>
									<div className="validator">
										<i
											className={
												userName.error.character
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: userName.error.character
													? "green"
													: "red",
											}}
										>
											At least one letter
										</p>
									</div>
									<div className="validator">
										<i
											className={
												userName.error.specialCharacters
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: userName.error
													.specialCharacters
													? "green"
													: "red",
											}}
										>
											No special characters
										</p>
									</div>
								</div>
							) : (
								""
							)}
						</Form.Group>
						<Form.Group controlId="password">
							<Form.Control
								autoComplete="new-password"
								value={password.value}
								// onBlur={formic.handleBlur}
								onChange={handlePasswordChange}
								type="password"
								placeholder="Create a Password"
								required
							/>
							{password.isFocused ? (
								<div className="validation">
									<div className="validator">
										<i
											className={
												password.error.strLength
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: password.error.strLength
													? "green"
													: "red",
											}}
										>
											Minimum 8 characters
										</p>
									</div>
									<div className="validator">
										<i
											className={
												password.error.number
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: password.error.number
													? "green"
													: "red",
											}}
										>
											At least one number
										</p>
									</div>
									<div className="validator">
										<i
											className={
												password.error.character
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: password.error.character
													? "green"
													: "red",
											}}
										>
											At least one letter
										</p>
									</div>
								</div>
							) : (
								""
							)}
						</Form.Group>

						{/* </div> */}
					</div>
				</div>
				<div className="row">
					<div className="col-md-12 wide-10">
						{props.registrationError && (
							<Alert variant="danger ">
								{props.registrationError}
							</Alert>
						)}
					</div>
				</div>
				<div className="text-center">
					<Button type="submit" variant="primary loginBtn">
						Create Account
					</Button>
				</div>
			</Form>
		</div>
	);
};

function mapStateToProps(state) {
	return {
		registrationError: state.errors.fullRegistrationError,
	};
}

export default connect(mapStateToProps)(CreateAccountForm);
