import * as CQRoutes from "./routes";
import Login from "./routes/login";
import SignUp from "./routes/sign_up";
import VerifyPhone from "./routes/verify";

import AssessmentReportLocked from "./routes/report";

import AssessmentSubmit from "./routes/assessment_submit";

import AssessmentResult from "./routes/assessment_result";

import CouponCheckout from "./routes/payment";
import AboutUs from "./components/about-us";
import Terms from "./components/Terms";
import Privacy from "./components/Privacy";

const routes = [
	{
		path: "/",
		exact: true,
		component: CQRoutes.Home,
	},
	{
		path: "/home",
		exact: true,
		component: CQRoutes.Home,
	},
	{
		path: "/about-us",
		exact: true,
		component: AboutUs,
	},
	{
		path: "/assessment",
		component: CQRoutes.Assessment,
	},
	{
		path: "/assessment-submit",
		component: AssessmentSubmit,
	},
	{
		path: "/assessment-result",
		component: AssessmentResult,
		protected: true,
		redirect: "/login",
	},
	{
		path: "/sign-up",
		exact: true,
		component: SignUp,
	},
	{
		path: "/login",
		exact: true,
		component: Login,
	},
	{
		path: "/verify-phone",
		exact: true,
		component: VerifyPhone,
	},
	{
		path: "/report",
		component: AssessmentReportLocked,
		protected: true,
		redirect: "/login",
	},
	{
		path: "/checkout",
		component: CouponCheckout,
		protected: true,
		redirect: "/login",
	},
	{
		path: "/terms",
		exact: true,
		component: Terms,
	},
	{
		path: "/privacy",
		exact: true,
		component: Privacy,
	},
	{
		path: "*",
		component: CQRoutes.PageNotFound,
	},
];

export default routes;
