import React from "react";
import QuestionCard from "../components/cards";
import { ReactComponent as RedoIcon } from "../assets/svgs/redo-solid.svg";

export const AssessmentQuestion = ({
	allowMultipleSelection,
	index,
	isRedoAvailable,
	question,
	option,
	setAnswer,
	onRedo,
	questionLength = 50,
	disabled,
}) => {
	return (
		<section className="mainQuestnsBlog">		

			<div className="mainQuestnsBlogCnt">
				{index < 24 ? (
					<p className="chooseAns mt-auto">Tap an Image To Choose</p>
				) : (
					<p className="chooseAns mt-auto">Pick an Answer</p>
				)}
				<h2 className=" h6 questionsHeading text-muted font-weight-bold">
					Question: {index + 1} / {questionLength}
				</h2>
				<div className="progress">
					<div
						className="progress-bar"
						style={{
							width: ((index + 1) / questionLength) * 100 + "%",
						}}
					/>
				</div>
				
				<button
					className="btn realodBtn btn-sm btn-outline font-weight-bold"
					disabled={!isRedoAvailable}
					onClick={onRedo}
				>
					<RedoIcon className="redo-icon" />
					{/*<img alt="Redo Icon" className="mr-2 my-auto" height='16' width='16' src="" />*/}
					<span className="my-auto redo_quest">Redo Last question</span>
				</button>
				<QuestionCard
					allowMultipleSelection={allowMultipleSelection}
					shouldTransition={false}
					setAnswer={setAnswer}
					question={question}
					index={index}
					disabled={disabled}
				/>
			</div>
		</section>
	);
};
