import React, { useEffect, useRef, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { LoginForm, LoginWithPassword, ResetPasswordForm } from "./auth";
import {
	Container,
	Row,
	Col,
	ProgressBar,
	Nav,
	Form,
	Button,
	Modal,
	Navbar,
	Alert,
	Carousel,
} from "react-bootstrap";
import { Link, useHistory, useLocation } from "react-router-dom";
import { withRouter } from "react-router";

import ReCAPTCHA from "react-google-recaptcha";
import { useFormik } from "formik";
import intro from "../data/intro.json";
import CQAPI from "../api";
import moment from "moment";
// import { navBarMenuItems } from "../config";

const AssessmentCheckModal = (props) => {
	const dispatch = useDispatch();
	const history = useHistory();
	const onClick = (path) => {
		history.push(path);
		dispatch({ type: "closeAssessmentCheckModal" });
	};
	return (
		<Modal
			show={props.show}
			onHide={props.onClose}
			centered
			backdrop="static"
		>
			<Modal.Header closeButton />
			<Modal.Body className="text-center">
				<p> Have you taken the CQ Assessment before?</p>
				<p>
					<Button
						className="btn btn-primary btn-outline cqBtn"
						onClick={() => onClick("/login")}
					>
						Yes
					</Button>

					<Button
						className="btn btn-primary cqBtn"
						onClick={() => onClick("/assessment")}
					>
						No
					</Button>
				</p>
			</Modal.Body>
		</Modal>
	);
};

const AssessmentHalfWay = ({ data, onContinue }) => {
	return (
		<div className="mainQuestnsBlog">
			<div className="quesBlog_04">
				<img
					src={data.imageSrc}
					className="relax_pic"
					alt="Assessment Complete"
				/>
				<div className="">
					<p className="strong">{data.title}</p>
					<p className="mb-3">{data.text}</p>
					<ProgressBar now={data.percentComplete} />
					<p className="font-weight-bold">
						{data.percentComplete}% complete
					</p>
					<Button variant="primary cqBtn" onClick={onContinue}>
						Continue
					</Button>
				</div>
			</div>
		</div>
	);
};

const AssessmentCompleteHeader = (props) => (
	<div>
		{/* <img
			src="../assets/breaks/goal-animate.svg"
			alt="Assessment Complete"
		/> */}
		{/* <img className="my-5" src="/assets/svgs/assessment_complete.png" alt="Assessment Complete" /> */}
		<div className="my-5">
			<p className="h4 font-weight-bold cq-color">Congratulations!</p>
			<p>Youve completed the CQ Assessment</p>
		</div>
	</div>
);

const AssessmentCompleteFooter = (props) => {
	const viewReportButtonRef = useRef(null);
	useEffect(() => {
		viewReportButtonRef.current?.scrollIntoView();
	}, [props.isSubmissionComplete]);
	return props.isSubmissionComplete ? (
		<div>
			<p className="font-weight-bold">Your Results Are Ready To View</p>
			<p>
				Please take a look at your analytics and your future career
				oppurtunities!
			</p>
			<Link
				className="btn btn-primary viewReportBtn cqBtn"
				to="/report"
				ref={viewReportButtonRef}
			>
				View Report
			</Link>
			&nbsp;
		</div>
	) : (
		<div>
			<p className="font-weight-bold">Result Is Being Calculated</p>
			<p className="text-muted">
				Please give us some time to fetch the results for you
			</p>
			<br />
			<div className="m-auto">
				<ProgressBar animated={true} now={90} min={0} max={100} />
			</div>
		</div>
	);
};

const AssessmentComplete = (props) => {
	if (props.report === "error") {
		return (
			<Container className="expanded" fluid>
				<Row className="justify-content-center">
					<Col className="text-center" md={4}>
						{/* <img
							className="my-5"
							src="/assets/svgs/assessment_complete.png"
							alt="Assessment Complete"
						/> */}
						<p className="font-weight-bold">
							Your Results Are Ready To View
						</p>
						<p>
							Please take a look at your analytics and your future
							career oppurtunities!
						</p>
						<Link>Sign Up</Link>
					</Col>
				</Row>
			</Container>
		);
	} else {
		return (
			<div className="fullscreen flex-center">
				<Row className="justify-content-center">
					<div className="text-center assessment_result">
						<AssessmentCompleteHeader />
						<AssessmentCompleteFooter
							isSubmissionComplete={props.isSubmissionComplete}
						/>
					</div>
				</Row>
			</div>
		);
	}
};

const AssessmentIntroduction = ({ onStart }) => {
	const [slideIndex, setSlideIndex] = useState(0);
	return (
		<Carousel interval={null} indicators={false} activeIndex={slideIndex}>
			{intro.map((slide, index) => (
				<Carousel.Item key={index}>
					<AssessmentIntroductionSlide {...slide} />
					<footer className="mt-1 text-center">
						{index === 2 ? (
							<Button variant="primary cqBtn" onClick={onStart}>
								Start
							</Button>
						) : (
							<Button
								variant="primary cqBtn"
								onClick={() => setSlideIndex(index + 1)}
							>
								Next
							</Button>
						)}
					</footer>
				</Carousel.Item>
			))}
		</Carousel>
	);
};

const AssessmentIntroductionSlide = ({
	index,
	title,
	text,
	imageSrc,
	infoText,
	onStart,
	onNext,
}) => {
	return (
		<article className="text-center">
			<header></header>
			<section className="row justify-content-center align-items-center">
				<div className="col-md-5 ">
					<h1 className="font-weight-bold assHeading mt-20">
						CQ Assessment
					</h1>
					<img className="cq_img" src={imageSrc} alt="" />
					<h3 className="font-weight-bold">{title}</h3>
					<p className="text-landing">{text}</p>
					{/* <p className="text-gold h6 font-weight-bold my-4">Ready to get started?</p> */}
					<div>
						{/* <a className="text-muted" href="">{infoText}</a> */}
					</div>
				</div>
			</section>
		</article>
	);
};

const CQappbar = (props) => {
	const [expanded, setExpanded] = useState(false);
	const dispatch = useDispatch();
	const location = useLocation();
	useEffect(() => {
		if (expanded) {
			document
				.getElementById("main-content")
				.addEventListener("click", function (event) {
					setExpanded(!expanded);
					event.preventDefault();
				});
		}
	}, [expanded]);
	useEffect(() => {
		if (
			props.user &&
			props.tokens &&
			moment(props.tokens.access.expires).format() <= moment().format()
		) {
			if (
				moment(props.tokens.refresh.expires).format() >
				moment().format()
			) {
				props.newAccessToken();
			} else {
				props.logOut();
			}
		}
	}, [location]);
	const logout = () => {
		props.history.push("/login");
		props.logOut();
		handleCloseToggle();
	};
	const handleCloseToggle = () => {
		setTimeout(() => setExpanded(false), 150);
	};
	return (
		<Navbar collapseOnSelect expand="lg" expanded={expanded}>
			<Navbar.Brand>
				<Link to="/">
					<img
						className="brand"
						src="assets/CareerQuotient.png"
						alt="Career Quotient Logo"
					/>
				</Link>
			</Navbar.Brand>
			<Navbar.Toggle
				aria-controls="responsive-navbar-nav"
				onClick={() => setExpanded(!expanded)}
			/>
			<Navbar.Collapse id="responsive-navbar-nav">
				<Nav className="ml-auto">
					<Nav.Link>
						<Link to="/" onClick={handleCloseToggle}>
							Home
						</Link>
					</Nav.Link>
					<Nav.Link>
						<Link to="/about-us" onClick={handleCloseToggle}>
							About Us
						</Link>
					</Nav.Link>
					{/* <Nav.Link>
						<Link
							onClick={() => {
								dispatch({ type: "openAssessmentCheckModal" });
								handleCloseToggle();
							}}
						>
							CQ Assessment
						</Link>
					</Nav.Link> */}
					{!["/login", "/verify-phone"].includes(
						location.pathname
					) ? (
						props.user && props.tokens ? (
							<Nav.Link>
								<Link onClick={logout} to={"/"}>
									Logout
								</Link>
							</Nav.Link>
						) : (
							<Nav.Link>
								<Link to={"/login"} onClick={handleCloseToggle}>
									Login
								</Link>
							</Nav.Link>
						)
					) : (
						""
					)}
				</Nav>
			</Navbar.Collapse>
		</Navbar>
	);
};

function mapStateToProps(state) {
	return { user: state.auth.user, tokens: state.auth.tokens };
}

const mapDispatchToProps = (dispatch) => {
	return {
		logOut: () => dispatch({ type: "auth/removeUser" }),
		newAccessToken: () => dispatch({ type: "auth/getNewAccessToken" }),
	};
};

const _Appbar = withRouter(CQappbar);

const Appbar = connect(mapStateToProps, mapDispatchToProps)(_Appbar);

const Loading = (props) => {
	return <div>Loading...</div>;
};

const ContactForm = (props) => {
	const captchaRef = useRef(null);
	const [submittingForm, setSubmittingForm] = useState(false);
	const [sentSuccessfully, setSentSuccessfully] = useState(false);

	const formik = useFormik({
		initialValues: {
			contactFormName: "",
			contactFormEmail: "",
			contactFormQuery: "",
			accelerateCareerProgess: false,
			assessmentForEmployees: false,
			forStudentsTraining: false,
		},
		onSubmit: async (values) => {
			setSubmittingForm(true);
			const data = values;
			data.email = data.contactFormEmail;
			const token = await captchaRef.current.executeAsync();
			data["g-recaptcha-response"] = token;
			captchaRef.current.reset();
			console.log(data);
			await CQAPI.enquiry(data).then((resp) => {
				return resp;
			});
			setSentSuccessfully(true);
			setSubmittingForm(false);
			formik.resetForm();
		},
		validate: (values) => {
			let errors = {};
			if (!values.contactFormName) {
				errors.contactFormName = "Name is required";
			}
			if (!values.contactFormEmail) {
				errors.contactFormEmail = "Email is required";
			}
			if (
				!/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
					values.contactFormEmail.toLowerCase()
				)
			) {
				errors.contactFormEmail = "Enter valid email";
			}
			if (!values.contactFormQuery) {
				errors.contactFormQuery =
					"Please describe your query in a few words below";
			}
			return errors;
		},
	});

	return (
		<Form noValidate onSubmit={formik.handleSubmit}>
			<ReCAPTCHA
				ref={captchaRef}
				size="invisible"
				sitekey="6LcKvHoaAAAAAA8Bh-JzbsJWBqTYbLjx_5g82ngA"
			/>
			<Form.Group controlId="contactFormName">
				{formik.errors.contactFormName &&
				formik.touched.contactFormName ? (
					<Alert variant="danger" className="errorMsg">
						{formik.errors.contactFormName}
					</Alert>
				) : null}
				<Form.Control
					type="text"
					placeholder="Full Name"
					value={formik.values.contactFormName}
					onChange={formik.handleChange}
					onBlur={formik.handleBlur}
					isValid={
						formik.touched.contactFormName &&
						!formik.errors.contactFormName
					}
				/>
			</Form.Group>
			<Form.Group controlId="contactFormEmail">
				{formik.errors.contactFormEmail &&
				formik.touched.contactFormEmail ? (
					<Alert variant="danger" className="errorMsg">
						{formik.errors.contactFormEmail}
					</Alert>
				) : null}
				<Form.Control
					type="email"
					placeholder="Email ID"
					value={formik.values.contactFormEmail}
					onChange={formik.handleChange}
					onBlur={formik.handleBlur}
					isValid={
						formik.touched.contactFormEmail &&
						!formik.errors.contactFormEmail
					}
				/>
			</Form.Group>
			<Form.Group controlId="contactFormQuery">
				{formik.errors.contactFormQuery &&
				formik.touched.contactFormQuery ? (
					<Alert variant="danger" className="errorMsg">
						{formik.errors.contactFormQuery}
					</Alert>
				) : null}
				<Form.Control
					type="text"
					placeholder="Tell us how we can help you"
					value={formik.values.contactFormQuery}
					onChange={formik.handleChange}
					onBlur={formik.handleBlur}
					isValid={
						formik.touched.contactFormQuery &&
						!formik.errors.contactFormQuery
					}
				/>
			</Form.Group>
			<Form.Group className="mt-0 mb-5">
				<Form.Label>You Want To:</Form.Label>
				<Form.Check
					className="my-1"
					type="checkbox"
					label="Accelerate Your Career Progress"
					checked={formik.values.accelerateCareerProgess}
					onChange={(e) =>
						formik.setFieldValue(
							"accelerateCareerProgess",
							e.target.checked
						)
					}
				/>
				<Form.Check
					className="my-1"
					type="checkbox"
					label="Run an assessment for your employees"
					checked={formik.values.assessmentForEmployees}
					onChange={(e) =>
						formik.setFieldValue(
							"assessmentForEmployees",
							e.target.checked
						)
					}
				/>
				<Form.Check
					className="my-1"
					type="checkbox"
					label="Partner with us for your students training"
					checked={formik.values.forStudentsTraining}
					onChange={(e) =>
						formik.setFieldValue(
							"forStudentsTraining",
							e.target.checked
						)
					}
				/>
			</Form.Group>
			<Button
				type="submit"
				variant="primary"
				className="assesBtn mb-2"
				block
				disabled={submittingForm}
			>
				Send
			</Button>
			{sentSuccessfully ? (
				<Alert
					variant="success"
					dismissible
					onClose={() => setSentSuccessfully(false)}
				>
					Sent!
				</Alert>
			) : null}
		</Form>
	);
};

export { AssessmentQuestion } from "./assessment.js";

export {
	Appbar,
	LoginForm,
	LoginWithPassword,
	ResetPasswordForm,
	ContactForm,
	AssessmentIntroduction,
	AssessmentComplete,
	AssessmentHalfWay,
	AssessmentIntroductionSlide,
	Loading,
	AssessmentCheckModal,
};
