import React, { useState } from "react";

const questionChangeDelay = 600;
const duration = 1000;

// const defaultStyle = {
// 	transition: `opacity ${duration}ms ease-in-out`,
// 	opacity: 0,
// };

const QuestionCard = (props) => {
	const { question, disabled } = props;
	const { options } = question;
	// const [canExit , setExit] = useState(false);
	const optionsList = options.map((option, index) => (
		<Choice
			key={index}
			option={option}
			index={index}
			disabled={disabled}
			allowMultipleSelection={props.allowMultipleSelection}
			onClick={(selectedOption) =>
				props.setAnswer({
					question: question.id,
					selectedOption: selectedOption,
				})
			}
		/>
	));

	return (
		<div className="ansChooseBlog mt-3 mb-5">
            <div className="ansImgBlog">
			<div className="row justify-content-center mb-2">
				<p className="">
					{"imageUrl" in options[0] ? null : (
						<em className="text-muted quetText text-center">
							Choose the answer that is closer to how you normally
							feel or behave.
							
						</em>
					)}
					<span className="font-weight-bold displayBlock mt-20">{question.text}</span>
				</p>
				{optionsList}
			</div>
            </div>
			{/* {"imageUrl" in options[0] ? (
				<p className="text-muted mt-auto">Tap an Image To Choose</p>
			) : (
				<p className="text-muted mt-auto">Pick an Answer</p>
			)} */}
			{/* {props.allowMultipleSelection ? (
				<Button variant="primary">Submit</Button>
			) : (
				<></>
			)} */}
		</div>
	);
};

const ChoiceImageItem = (props) => {
	const [selected, setSelected] = useState(false);
	const onSelect = (option) => {
		setSelected(true);
		setTimeout(() => {
			setSelected(false);
			props.onClick(option);
		}, questionChangeDelay);
	};
	return (
		<div>
			<div
				onClick={() => onSelect(props.option._id)}
				className="option-image-wrapper"
			>
				<div className="position-relative">
					<div
						className={
							"selection-overlay flex-center flex-column" +
							(selected ? " selected" : "")
						}
					>
						<img
							alt="Circle Check"
							className="mr-2 my-auto"
							height="20%"
							width="20%"
							src="../assets/svgs/check-circle-regular.svg"
						/>
						{/* <img className="option-image" style={{opacity:.0}} src={props.option.imageUrl} /> */}
					</div>
					<img
						className="option-image"
						src={props.option.imageUrl}
						alt={props.option.text}
					/>
				</div>
			</div>
			<p className="optionAns">
				{props.option.text}
			</p>
		</div>
	);
};

const ChoiceTextItem = (props) => {
	const [selected, setSelected] = useState(false);
	const onSelect = (option) => {
		setSelected(true);
		setTimeout(() => {
			setSelected(false);
		}, questionChangeDelay);
	};
	return (
		<div onClick={onSelect}>
			<p
				onClick={() => props.onClick(props.option._id)}
				className={
					"option-text font-weight-medium text-lowercase my-2 mx-auto" +
					("imageUrl" in props.option ? "" : " border-10 py-2") +
					(selected ? " selected" : "")
				}
				style={{ maxWidth: 800 + "px" }}
			>
				{props.option.text}
				{/* <span>
                    <OverlayTrigger
                    placement='right'
                    overlay={
                        <Tooltip>
                        <p>{props.option.helpText}</p>
                        </Tooltip>
                    }>
                    <img className="ml-2" height='24' width='24' src="../assets/svgs/question-circle-regular.svg" />
                    </OverlayTrigger>
                </span> */}
			</p>
		</div>
	);
};

const Choice = ({ option, onClick, disabled }) => {
	const [selected, setSelected] = useState(false);
	const onSelect = (option) => {
		onClick(option);
		// setSelected(disabled);
	};
	return (
		<div
			className={
				"choice col col-12 " + ("imageUrl" in option ? "col-sm-6" : "")
			}
			disabled={selected}
		>
			{/* <CSSTransition in={selected} timeout={1000} classNames="selected-choice"> */}
			{"imageUrl" in option ? (
				<ChoiceImageItem option={option} onClick={onSelect} />
			) : (
				<ChoiceTextItem option={option} onClick={onSelect} />
			)}
			{/* </CSSTransition> */}
		</div>
	);
};

export default QuestionCard;
