import React, { useState } from "react";
import { connect } from "react-redux";
import { Col } from "react-bootstrap";
import { LoginForm, LoginWithPassword, ResetPasswordForm } from "../components";
// import { Link } from "react-router-dom";
import CQAPI from "../api";

// TODO: Extract to Child
export const Login = (props) => {
	const [user, setUser] = useState("");
	const [passwordErrors, setPasswordErrors] = useState("");
	const [passwordRequired, setPasswordRequired] = useState(false);
	const [resettingPassword, setResettingPassword] = useState(false);
	const [forgotPasswordMessage, setForgotPasswordMessage] = useState("");
	const handleLogin = (value) => {
		props.onSuccess({
			user: value.user,
			tokens: value.tokens,
		});
		props.onLogin({ loginMessage: value.message });
		if (!value.passwordRequired) {
			props.history.push("/verify-phone?redirect=assessment-result");
		}
		console.log(props);
	};
	const handleLoginWithPassword = (value) => {
		CQAPI.signIn({
			userId: user.user.id,
			credentials: { password: value },
		})
			.then((resp) => {
				props.onSuccess({
					user: resp.user,
					tokens: resp.tokens,
				});
				props.history.push(`/assessment-result`);
			})
			.catch((err) => {
				setPasswordErrors(err.message);
			});
	};
	const handleOTPRequest = () => {
		CQAPI.resendOTP(user.user.mobile.number)
			.then((resp) => {
				if (resp.otpSent) {
					setPasswordRequired(false);
					props.onSuccess({ user: resp.user, tokens: resp.tokens });
					props.history.push(
						"/verify-phone?redirect=assessment-result"
					);
				}
			})
			.catch((err) => {
				setPasswordErrors(err.message);
			});
	};
	const handleForgotPassword = () => {
		CQAPI.forgotPassword(user.user.email[0]?.address)
			.then((resp) => {
				if (resp.otpSent) {
					setResettingPassword(true);
					setForgotPasswordMessage(resp.message);
				}
			})
			.catch((err) => {
				setPasswordErrors(err.message);
			});
	};
	const handleResetPassword = (data) => {
		CQAPI.resetPassword(user.user.id, data.otp, data.password)
			.then((resp) => {
				setResettingPassword(false);
				alert(resp.message);
				setPasswordRequired(true);
			})
			.catch((err) => {
				setPasswordErrors(err.message);
			});
	};
	return (
		<div className="fullscreen flex-center flex-column">
			<div className="loginpage">
				{resettingPassword ? (
					<ResetPasswordForm
						handleResetPassword={handleResetPassword}
						forgotPasswordMessage={forgotPasswordMessage}
						user={user}
					/>
				) : passwordRequired ? (
					<LoginWithPassword
						errors={props.errors}
						onSubmit={handleLoginWithPassword}
						onOTPRequest={handleOTPRequest}
						setUser={setUser}
						passwordErrors={passwordErrors}
						user={user.user}
						handleForgotPassword={handleForgotPassword}
					/>
				) : (
					<LoginForm
						setUser={setUser}
						errors={props.errors}
						onSubmit={handleLogin}
						setPasswordRequired={setPasswordRequired}
					/>
				)}
			</div>
		</div>
	);
};

function mapStateToProps(state) {
	return {
		user: state.auth.user,
		errors: state.auth.error,
	};
}

const mapDispatchToProps = (dispatch) => {
	return {
		onSuccess: (payload) =>
			dispatch({ type: "auth/setUser", payload: payload }),
		signIn: (credentials) =>
			dispatch({ type: "auth/signIn", payload: credentials }),
		onLogin: (message) =>
			dispatch({ type: "auth/loginMessage", payload: message }),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);
