import { AssessmentComplete } from "../components";
import { connect } from "react-redux";
import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import CQAPI from "../api";

const AssessmentResult = (props) => {
	let history = useHistory();
	const [isComplete, setComplete] = useState(false);
	const [error, setError] = useState();
	useEffect(() => {
		CQAPI.getLastReport(props.user.id, props.token.access.token)
			.then((resp) => {
				props.onSuccess(resp);
				setComplete(true);
			})
			.catch((err) => {
				if (err.message === "Submission not found") {
					history.push("assessment");
				} else {
					setError(err.message);
				}
			});
	}, []);
	return (
		<div>
			{error ? (
				<p>{error}</p>
			) : (
				<AssessmentComplete isSubmissionComplete={isComplete} />
			)}
		</div>
	);
};

function mapStateToProps(state) {
	return {
		token: state.auth.tokens,
		user: state.auth.user,
	};
}

const mapDispatchToProps = (dispatch) => {
	return {
		onSuccess: (report) =>
			dispatch({ type: "assessment/reportResponse", payload: report }),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(AssessmentResult);
