import { AssessmentComplete } from "../components";
import { connect } from "react-redux";
import { useEffect, useState } from "react";
import CQAPI from "../api";

const AssessmentSubmit = (props) => {
	const [isComplete, setComplete] = useState(false);
	const [error, setError] = useState();
	useEffect(() => {
		if (props.assessment?.responses) {
			CQAPI.submitResponse(
				props.assessment.responses,
				props.tokens.access.token
			)
				.then((resp) => {
					props.onSuccess(resp);
					setComplete(true);
				})
				.catch((err) => setError(err.toString()));
		} else {
			props.history.push("assessment");
		}
	}, []);
	return (
		<div>
			{error ? (
				<p>{error}</p>
			) : (
				<AssessmentComplete isSubmissionComplete={isComplete} />
			)}
		</div>
	);
};

function mapStateToProps(state) {
	return {
		tokens: state.auth.tokens,
		assessment: state.assessment,
	};
}

const mapDispatchToProps = (dispatch) => {
	return {
		onSuccess: (report) =>
			dispatch({ type: "assessment/reportResponse", payload: report }),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(AssessmentSubmit);
