import { connect } from "react-redux";
import { Col, Container, Alert } from "react-bootstrap";
import { SignUpForm } from "../components/auth";
import React, { useState } from "react";
import CQAPI from "../api";

export const SignUp = (props) => {
	const [error, setError] = useState();
	const [disabled, setDisabled] = useState(false);
	// const params = new URLSearchParams(props.history.location.search);
	// const redirect = params.get('redirect') || 'home'
	const handleLogin = (user) => {
		setDisabled(true);
		CQAPI.signUp(user)
			.then((resp) => {
				setDisabled(false);
				props.onSuccess({ user: resp.user, tokens: resp.tokens });
				props.history.push(
					`/verify-phone?redirect=${
						resp.reportExist
							? "assessment-result"
							: "assessment-submit"
					}`
				);
				if (resp.reportExist) {
					props.clearAnswers(null);
				}
			})
			.catch((err) => {
				setError(err.message);
				setDisabled(false);
			});
	};
	return (
		<Container>
			<div className="fullscreen flex-center">
				<Col sm={8} md={6} lg={6}>
					{/* {error && <Alert variant="danger">{error}</Alert>} */}
					<SignUpForm
						onSubmit={handleLogin}
						submitErrors={error}
						btnDisabled={disabled}
					/>
				</Col>
			</div>
		</Container>
	);
};

function mapStateToProps(state) {
	return { user: state.auth.user };
}

const mapDispatchToProps = (dispatch) => {
	return {
		onSuccess: (user) => dispatch({ type: "auth/setUser", payload: user }),
		clearAnswers: (answers) =>
			dispatch({ type: "assessment/completed", payload: null }),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(SignUp);
