import "./styles/App.scss";
import { Appbar, AssessmentCheckModal } from "./components";
import {
	HashRouter as Router,
	Switch,
	Route,
	useLocation,
} from "react-router-dom";
import routes from "./router";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import ProtectedRoute from "./routes";
import { useDispatch, useSelector } from "react-redux";
import ScrollToTop from "./helpers/ScrollToTop";

const AnimatedApp = () => {
	const location = useLocation();
	return (
		<TransitionGroup>
			<CSSTransition key={location.key} classNames="fade" timeout={0}>
				<div id="main-content">
					<ScrollToTop />
					<Switch>
						{routes.map((route, index) =>
							route.protected ? (
								<ProtectedRoute key={index} {...route} />
							) : (
								<Route key={index} {...route} />
							)
						)}
					</Switch>
				</div>
			</CSSTransition>
		</TransitionGroup>
	);
};

function App() {
	const show = useSelector(
		(state) => state.assessment.showAssessmentCheckModal
	);
	const dispatch = useDispatch();
	return (
		<div className="App">
			<Router>
				<Appbar />
				<AnimatedApp />
				<AssessmentCheckModal
					show={show}
					onClose={() =>
						dispatch({ type: "closeAssessmentCheckModal" })
					}
				/>
			</Router>
		</div>
	);
}

export default App;
