import { Link } from "react-router-dom";

export const PageNotFound = () => (
	<div>
		<div className="flex-center flex-column">
			<img
				className="img-fluid"
				style={{ maxWidth: 1000 + "px" }}
				src="assets/svgs/404.svg"
				alt="404"
			/>
			<p>
				Page not Found please return go back to{" "}
				<Link style={{ color: "dodgerblue" }} to="/">
					Home page
				</Link>
			</p>
		</div>
	</div>
);
