import { API_HOSTNAME } from "./config";

class _CQAPI {
	//
	isErrorStatus(status) {
		console.log(status);
		return Math.floor(status / 100) > 3;
	}

	async dispatchRequest(path, data, method = "GET", token = null) {
		let endpoint = `${API_HOSTNAME}/${path}`;
		let params = {};
		let headers = {
			"Content-Type": "application/json",
			"Cache-Control": "no-store",
		};
		if (token) {
			headers["Authorization"] = `Bearer ${token}`;
		}
		if (data) {
			params.body = JSON.stringify(data);
		}
		params.headers = headers;
		params.method = method;

		const response = await fetch(endpoint, params);
		const json = await response.json();
		if (this.isErrorStatus(response.status)) {
			throw Error(json["message"]);
		}
		return json;
	}

	findUser(emailAddress) {
		return this.dispatchRequest(
			"auth/login/checkUserExist",
			{ userData: emailAddress },
			"POST"
		).then((resp) => {
			// console.log(resp)
			return resp;
		});
	}

	resendOTP(userLoginInfo) {
		return this.dispatchRequest(
			"auth/login/checkUserExist",
			{
				userData: userLoginInfo,
				forceOTP: true,
			},
			"POST"
		).then((resp) => {
			// console.log(resp)
			return resp;
		});
	}

	signIn({ userId, credentials }) {
		return this.dispatchRequest(
			`auth/login?userId=${userId}`,
			credentials,
			"POST"
		).then((resp) => {
			localStorage.setItem("user", JSON.stringify(resp));
			return resp;
		});
	}

	signUp(user) {
		return this.dispatchRequest("users/createUser", user, "POST");
	}
	fullRegistration(userId, user, accessToken) {
		return this.dispatchRequest(
			`users/updateUsernameAndPassword?userId=${userId}`,
			user,
			"POST",
			accessToken
		).then((resp) => {
			const localUser = JSON.parse(localStorage.getItem("user"));
			localUser.user = resp;
			localStorage.setItem("user", JSON.stringify(localUser));
			// console.log(resp);
			return resp;
		});
	}
	getQuestions() {
		return this.dispatchRequest("questions").then((resp) => {
			// console.log(resp)
			return resp;
		});
	}

	submitResponse(assessmentResponse, accessToken) {
		return this.dispatchRequest(
			"submissions",
			{ responses: assessmentResponse },
			"POST",
			accessToken
		).then((resp) => {
			// console.log(resp);
			localStorage.setItem("report", JSON.stringify(resp));
			return resp;
		});
	}

	getLastReport(userId, accessToken) {
		return this.dispatchRequest(
			`submissions/user/${userId}`,
			"",
			"GET",
			accessToken
		).then((resp) => {
			// console.log(resp);
			localStorage.setItem("report", JSON.stringify(resp));
			return resp;
		});
	}

	verifyCoupon(coupon, accessToken) {
		return this.dispatchRequest(
			"coupon/apply",
			{ couponCode: coupon },
			"POST",
			accessToken
		).then((resp) => {
			console.log(resp);
			return resp;
		});
	}

	buyMembership(coupon, accessToken) {
		return this.dispatchRequest(
			"user-membership/buy",
			{ couponcode: coupon },
			"POST",
			accessToken
		).then((resp) => {
			console.log(resp);
			return resp;
		});
	}

	verifyPayment(paymentId, accessToken) {
		return this.dispatchRequest(
			"user-membership/verifyPayment",
			{ paymentId: paymentId },
			"POST",
			accessToken
		).then((resp) => {
			console.log("verifyPayment", resp);
			return resp;
		});
	}

	enquiry(data) {
		return this.dispatchRequest("enquiry", data, "POST").then((resp) => {
			return resp;
		});
	}

	forgotPassword(userData) {
		return this.dispatchRequest(
			"auth/forgot-password",
			{ userData },
			"POST"
		).then((resp) => {
			return resp;
		});
	}

	resetPassword(userId, otp, password) {
		return this.dispatchRequest(
			`auth/reset-password?userId=${userId}&token=${otp}`,
			{ password },
			"POST"
		).then((resp) => {
			return resp;
		});
	}

	getNewAccessToken(refreshToken) {
		return this.dispatchRequest(
			"auth/refresh-tokens",
			{ refreshToken: refreshToken },
			"POST"
		).then((resp) => {
			return resp;
		});
	}
}

const CQAPI = new _CQAPI();
export default CQAPI;
