const Hero = (props) => {
	const dispatch = useDispatch();

	return (
		<div className="hero-wrapper">
			<section className="container">
				<Row>
					<div className="col-md-5 hero_leftCnt">
						<h1 className="font-weight-bold mb-4">
							Lightspeed Career Progress Starts Now
						</h1>
						<p className="text-landing">
							Take the Career Quotient Assessment to understand
							yourself better, and find the right courses and
							opportunities to take your career to the next level.
						</p>
						<div
							onClick={() =>
								dispatch({ type: "openAssessmentCheckModal" })
							}
							className="btn btn-lg btn-primary font-weight-bold assesBtn"
							to="/assessment"
						>
							Take CQ Assessment
						</div>
					</div>
					<div className="col-md-7 hero_rightCnt">
						<img
							className="img-fluid"
							src="assets/svgs/hero.png"
							alt=""
						/>
					</div>
				</Row>
			</section>
		</div>
	);
};

const Contact = (props) => {
	return (
		<div id="contact-us">
			<div className="container">
				<div className="text-lg-center landing-section-header">
					<small>Contact Us</small>
					<h1 className="font-weight-bold">
						Interested In Knowing More?
					</h1>
				</div>
				<Col xl={{ span: 5, offset: 3 }} lg={{ span: 7, offset: 0 }}>
					<ContactForm />
				</Col>
			</div>
		</div>
	);
};

const TheProblem = () => {
	return (
		<div id="the-problem" className="">
			<div className="text-center landing-section-header ">
				<small>The Problem</small>
				<h1 className="font-weight-bold">
					What are we trying to solve?
				</h1>
			</div>
			<div className="container">
				<div className="row">
					{theProblemItems.map((item, index) => (
						<div className="col-md-3 pt-30 text-center" key={index}>
							<img
								className="mb-5"
								height="128"
								width="128"
								src={item.imageSrc}
							/>
							<p className="font-weight-bold">{item.title}</p>
							<p className="text-secondary">{item.text}</p>
						</div>
					))}
				</div>
			</div>
		</div>
	);
};

const OurApproach = (props) => {
	return (
		<div className="container">
			<Row className="justify-content-center mt-5 mt-xl-0">
				<Col className="text-lg-center landing-section-header" xl={8}>
					<small>Our Approach</small>
					<h1 className="font-weight-bold mt-10">
						Creating a Complete Ecosystem for Career Guidance
					</h1>
				</Col>
			</Row>
			<Row className="justify-content-around mt-30">
				{ourApproachItems.map((item, index) => (
					<Col
						xs={12}
						sm={6}
						md={4}
						lg={3}
						xl={2}
						className="bg-dark-circle mx-2 mb-4"
						key={index}
					>
						<div>
							<img
								className="mb-5"
								height="100"
								width="100"
								src={item.imageSrc}
							/>
							<p className="font-weight-bold">{item.title}</p>
							<p className="ecosystem_p text-secondary">
								{item.text}
							</p>
						</div>
					</Col>
				))}
				{/* <div className="overlay-line">
                    <img src="assets/svgs/line.svg" />
                </div> */}
			</Row>
		</div>
	);
};

const WhatWeBuild = (props) => {
	return (
		<div className="container">
			<div className="landing-section-header pb-30">
				<small>WE BUILD THE PATHWAY TO YOUR DREAMS</small>
				<h1 className="font-weight-bold">
					Career Quotient Was Built For
				</h1>
			</div>
			<div className="cqBuiltCnt bdr_top">
				<div className="row">
					{BuiltFor.map((item, index) => (
						<div className="col-md-4 fill" key={index}>
							<Row className="align-items-center mb-3">
								<p className="fill-leading">{`0${
									index + 1
								}`}</p>
								<h3 className="h3 font-weight-bold">
									{item.title}
								</h3>
							</Row>
							<p>{item.text}</p>
							{/* <a style={{ color: "white" }}>
								<img
									className="mr-2"
									height="40"
									width="40"
									src="assets/svgs/arrow-circle-right-solid.svg"
								/>
								Learn More
							</a> */}
						</div>
					))}
				</div>
			</div>
		</div>
	);
};

export default Homepage;
