import React, { useEffect, useState } from "react";
import { Alert, Form, Button, Container, Row, Col } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom";
import { connect } from "react-redux";
import { useFormik } from "formik";
import CQAPI from "../api";

export const LoginForm = ({
	onSubmit,
	errors,
	setPasswordRequired,
	setUser,
}) => {
	const [error, setError] = useState("");
	const history = useHistory();
	const formic = useFormik({
		initialValues: { username: "" },
		onSubmit: (value) => {
			CQAPI.findUser(value.username)
				.then((resp) => {
					if (resp.passwordRequired) {
						setUser(resp);
						setPasswordRequired(true);
					}
					onSubmit(resp);
					console.log(resp);
				})
				.catch((err) => {
					// errors[0] = err.message;
					setError(err.message);
				});
		},
		validate: (value) => {
			let errors = {};
			if (!value.username) {
				errors.username = "Required";
			}
		},
	});
	return (
		<div className="loginCnt">
			<div className="col-md-12">
				<div className="row">
					<div className="col-md-6 displayNone">
						<div className="form-top">
							<img
								className="loginLogo"
								src="assets/CareerQuotient_large.jpeg"
								alt="Career Quotient Logo"
							/>
						</div>
					</div>
					<div className="col-md-12">
						<div className="form-bottom">
							<Form
								className="loginForm"
								onSubmit={formic.handleSubmit}
							>
								<h2 className="title loginText text-center">
									Login
								</h2>
								<p>
									<b>Welcome to CQ.</b> Progress @ the speed
									of light
								</p>

								<div className="loginCTN">
									<Form.Group controlId="username">
										<Form.Control
											type="text"
											value={formic.values.username}
											onChange={formic.handleChange}
											onBlur={formic.handleBlur}
											placeholder="Enter Username/Email"
											required
										/>
									</Form.Group>
									{!error ? (
										""
									) : (
										<Alert variant="danger errorMsg">
											{error}
										</Alert>
									)}
									{formic.errors.username ? (
										<Alert variant="danger errorMsg">
											{formic.errors.username}
										</Alert>
									) : null}
								</div>

								<div className="text-left">
									<Button
										type={"submit"}
										variant="primary loginBtn"
									>
										Next
									</Button>
									{/* <div className="wide-30">
										<p className="mb-1">
											First time to CQ?&nbsp;
										</p>
										<NavLink to="/assessment">
											Take Assessment
										</NavLink>
									</div> */}
									<div className="wide-30">
										<p className="mb-1">
											First time to CQ?
											<span
												onClick={() =>
													history.push(`/assessment`)
												}
												className="cq-color text-bold ml-10"
												style={{ cursor: "pointer" }}
											>
												Take Assesment
											</span>
										</p>
									</div>
								</div>
							</Form>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export const SignUpForm = (props) => {
	const formic = useFormik({
		initialValues: {
			firstName: "",
			lastName: "",
			email: "",
		},
		onSubmit: (values, user) => {
			props.onSubmit(values);
		},
		validate: (values) => {
			let errors = {};
			if (!values.firstName) {
				errors.firstName = "First name is required";
			} else if (
				/[\d`!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(
					values.firstName
				)
			) {
				errors.firstName = "Do not enter special characters";
			}
			if (!values.lastName) {
				errors.lastName = "Last name is required";
			} else if (
				/[\d `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(
					values.lastName
				)
			) {
				errors.lastName = "Do not enter special characters";
			}
			if (!values.email) {
				errors.email = "email is required";
			} else if (
				!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
			) {
				errors.email = "Please provide valid email address";
			}
			return errors;
		},
	});
	return (
		<div className="signUP">
			<Form
				autoComplete="new-password"
				noValidate
				onSubmit={formic.handleSubmit}
			>
				<h2 className="title loginText text-center">Sign Up</h2>

				<Form.Group controlId="firstName">
					<Form.Control
						autoComplete="off"
						value={formic.values.firstName}
						onChange={formic.handleChange}
						type="text"
						minLength={1}
						onBlur={formic.handleBlur}
						placeholder="First Name"
						required
					/>
					{formic.errors.firstName && formic.touched.firstName ? (
						<Alert variant="danger errorMsg">
							{formic.errors.firstName}
						</Alert>
					) : null}
					{/* <Form.Control.Feedback type="invalid">{errors.firstName}</Form.Control.Feedback> */}
				</Form.Group>
				<Form.Group controlId="lastName">
					<Form.Control
						autoComplete="on"
						value={formic.values.lastName}
						onBlur={formic.handleBlur}
						minLength={1}
						onChange={formic.handleChange}
						type="text"
						placeholder="Last Name"
						required
					/>
					{formic.errors.lastName && formic.touched.lastName ? (
						<Alert variant="danger errorMsg">
							{formic.errors.lastName}
						</Alert>
					) : null}
					{/* <Form.Control.Feedback type="invalid">{errors.lastName}</Form.Control.Feedback> */}
				</Form.Group>
				<Form.Group controlId="email">
					<Form.Control
						autoComplete="new-password"
						value={formic.values.email}
						onBlur={formic.handleBlur}
						onChange={formic.handleChange}
						type="text"
						placeholder="Email"
						required
					/>
					{formic.errors.email && formic.touched.email ? (
						<Alert variant="danger errorMsg">
							{formic.errors.email}
						</Alert>
					) : null}
					{/* <Form.Control.Feedback type="invalid">{errors.mobile}</Form.Control.Feedback> */}
				</Form.Group>
				<p className="oneTime_inf">
					<small className="text-muted mt-10">
						OTP will be sent to the above entered email address
					</small>
				</p>
				{props.submitErrors && (
					<Alert variant="danger">{props.submitErrors}</Alert>
				)}
				<div className="wide-30">
					<Button
						type="submit"
						variant="primary loginBtn"
						disabled={props.btnDisabled}
					>
						Get OTP
					</Button>
				</div>
				{/* <p></p>
			<p>Already have an account?</p>
			<Link to="/login">Login</Link> */}
			</Form>
		</div>
	);
};

export const CreateAccountForm = (props) => {
	const [password, setPassword] = useState({
		value: "",
		error: {},
		isFocused: false,
	});
	const [userName, setUserName] = useState({
		value: "",
		error: {},
		isFocused: false,
	});
	const [email, setEmail] = useState({
		value: "",
		error: "",
	});
	const handleUserNameChange = (e) => {
		const value = e.target.value;
		if (password.isFocused) {
			setPassword({ ...password, isFocused: false });
		}
		setUserName({
			...userName,
			value: value,
			error: validateUserName(value),
			isFocused: true,
		});
	};
	const validateUserName = (value) => {
		return {
			strLength: value.length >= 8 && value.length <= 30,
			character: /[a-z]/.test(value),
			number: /[0-9]/.test(value),
			specialCharacters: !/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(
				value
			),
		};
	};
	const handlePasswordChange = (e) => {
		const value = e.target.value;
		if (userName.isFocused) {
			setUserName({ ...userName, isFocused: false });
		}
		setPassword({
			...password,
			value: value,
			error: validatePassword(value),
			isFocused: true,
		});
	};
	const validatePassword = (value) => {
		return {
			strLength: value.length >= 8,
			character: /[a-z A-Z]/.test(value),
			number: /[0-9]/.test(value),
		};
	};
	const handleChangeEmail = (e) => {
		const value = e.target.value;
		if (userName.isFocused) {
			setUserName({ ...userName, isFocused: false });
		}
		if (password.isFocused) {
			setPassword({ ...password, isFocused: false });
		}
		setEmail({ value, error: validateEmail(value) });
	};
	const validateEmail = (value) => {
		let error = "";
		if (!value) {
			error = "email is required";
		} else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
			error = "Please provide valid email address";
		}
		return error;
	};
	const handleSubmit = (event) => {
		event.preventDefault();
		const emailErrors = validateEmail(email.value);
		if (emailErrors) {
			setEmail({ ...email, error: emailErrors });
			return;
		}
		const userNameErrors = validateUserName(userName.value);
		if (Object.values(userNameErrors).includes(false)) {
			setUserName({
				...userName,
				isFocused: true,
				error: userNameErrors,
			});
			return;
		} else if (userName.isFocused) {
			setUserName({ ...userName, isFocused: false });
		}
		const passwordErrors = validatePassword(password.value);
		if (Object.values(passwordErrors).includes(false)) {
			setPassword({
				...password,
				isFocused: true,
				error: passwordErrors,
			});
			return;
		}
		const data = {
			userName: userName.value.toLowerCase(),
			password: password.value,
			email: email.value,
		};
		props.onSubmit(data);
	};
	// const formic = useFormik({
	// 	initialValues: {
	// 		// firstName: '',
	// 		// lastName: '',
	// 		email: "",
	// 		userName: "",
	// 		password: "",
	// 	},
	// 	onSubmit: (values) => {
	// 		values.userName = userName.value.toLowerCase();
	// 		values.password = password.value;
	// 		values.email = email.value;
	// 		if (email.error) {
	// 			return;
	// 		}
	// 		if (Object.values(userName.error).includes(false)) {
	// 			setUserName({ ...userName, isFocused: true });
	// 			return;
	// 		} else if (userName.isFocused) {
	// 			setUserName({ ...userName, isFocused: false });
	// 		}
	// 		if (Object.values(password.error).includes(false)) {
	// 			setPassword({ ...password, isFocused: true });
	// 			return;
	// 		}
	// 		props.onSubmit(values);
	// 	},
	// 	validate: (values) => {
	// 		let errors = {};
	// 		if (!values.firstName) {
	// 		    errors.firstName = 'First name is required'
	// 		} else if (/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(values.firstName)) {
	// 		    errors.firstName = 'Do not enter Special Character'
	// 		}
	// 		if (!values.lastName) {
	// 			errors.lastName = "Last name is required";
	// 		} else if (
	// 			/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(values.lastName)
	// 		) {
	// 			errors.lastName = "Do not enter Special Character";
	// 		}
	// 		if (!values.email) {
	// 			errors.email = "email is required";
	// 		} else if (
	// 			!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
	// 		) {
	// 			errors.email = "Please provide valid email address";
	// 		}
	// 		return errors;
	// 	},
	// });
	return (
		<div className="col-md-12">
			<Form
				autoComplete="new-password"
				noValidate
				onSubmit={handleSubmit}
			>
				<div className="row">
					<div className="col-md-6 leftBdr">
						<Form.Group controlId="firstName">
							<div className="row">
								<div className="col-md-4">
									<label>First Name</label>
								</div>
								<div className="col-md-8">
									<Form.Control
										autoComplete="off"
										type="text"
										minLength={1}
										placeholder="First Name"
										value={props.user.firstName}
										disabled
									/>
								</div>
							</div>
						</Form.Group>

						<Form.Group controlId="lastName">
							<div className="row">
								<label className="col-md-4">Last Name</label>
								<div className="col-md-8">
									<Form.Control
										autoComplete="on"
										className="col-md-8"
										value={props.user.lastName}
										minLength={1}
										type="text"
										placeholder="Last Name"
										disabled
									/>
								</div>
							</div>
						</Form.Group>
						<Form.Group controlId="mobile">
							<div className="row">
								<label className="col-md-4">Mobile</label>
								<div className="col-md-8">
									<Form.Control
										autoComplete="on"
										value={props.user.mobile.number}
										minLength={1}
										type="text"
										placeholder="Mobile"
										disabled
									/>
								</div>
							</div>
						</Form.Group>
					</div>
					<div className="col-md-6">
						<Form.Group controlId="email">
							<Form.Control
								autoComplete="new-password"
								value={email.value}
								// onBlur={formic.handleBlur}
								onChange={handleChangeEmail}
								type="text"
								placeholder="Email Id"
								required
							/>
							{email.error ? (
								<Alert variant="danger errorMsg">
									{email.error}
								</Alert>
							) : null}
							{/* <Form.Control.Feedback type="invalid">{errors.email}</Form.Control.Feedback> */}
						</Form.Group>
						<Form.Group controlId="userName">
							{/* <div className="toolTip"> */}
							<Form.Control
								autoComplete="new-password"
								value={userName.value}
								// onBlur={formic.handleBlur}
								onChange={handleUserNameChange}
								type="text"
								placeholder="Create a username"
								required
							/>
							{userName.isFocused ? (
								<div className="validation">
									<div className="validator">
										<i
											className={
												userName.error.strLength
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: userName.error.strLength
													? "green"
													: "red",
											}}
										>
											8-30 Characters
										</p>
									</div>
									<div className="validator">
										<i
											className={
												userName.error.number
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: userName.error.number
													? "green"
													: "red",
											}}
										>
											At least one number
										</p>
									</div>
									<div className="validator">
										<i
											className={
												userName.error.character
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: userName.error.character
													? "green"
													: "red",
											}}
										>
											At least one letter
										</p>
									</div>
									<div className="validator">
										<i
											className={
												userName.error.specialCharacters
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: userName.error
													.specialCharacters
													? "green"
													: "red",
											}}
										>
											No special characters
										</p>
									</div>
								</div>
							) : (
								""
							)}
						</Form.Group>
						<Form.Group controlId="password">
							<Form.Control
								autoComplete="new-password"
								value={password.value}
								// onBlur={formic.handleBlur}
								onChange={handlePasswordChange}
								type="password"
								placeholder="Create a Password"
								required
							/>
							{password.isFocused ? (
								<div className="validation">
									<div className="validator">
										<i
											className={
												password.error.strLength
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: password.error.strLength
													? "green"
													: "red",
											}}
										>
											Minimum 8 characters
										</p>
									</div>
									<div className="validator">
										<i
											className={
												password.error.number
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: password.error.number
													? "green"
													: "red",
											}}
										>
											At least one number
										</p>
									</div>
									<div className="validator">
										<i
											className={
												password.error.character
													? "fas fa-check success error-icon"
													: "fas fa-times error error-icon"
											}
										></i>
										<p
											className="validation-item"
											style={{
												color: password.error.character
													? "green"
													: "red",
											}}
										>
											At least one letter
										</p>
									</div>
								</div>
							) : (
								""
							)}
						</Form.Group>

						{/* </div> */}
					</div>
				</div>
				<div className="text-center">
					<Button type="submit" variant="primary loginBtn">
						Create Account
					</Button>
				</div>
			</Form>
		</div>
	);
};

