export const CouponForm = (props) => {
	// TODO: Get cost form server
	const originalCost = 199;
	const [verifiedCode, setVerifiedCode] = useState(false);
	const [error, setError] = useState();
	const [totalCost, setTotalCost] = useState(originalCost);
	const [discount, setDiscount] = useState(0);
	const [appliedCoupon, setAppliedCoupon] = useState("");

	const formic = useFormik({
		initialValues: {
			code: "",
		},
		onSubmit: () => {
			props.onSubmit(appliedCoupon, props.token);
		},
		validate: (values) => {
			if(values.code !== ''){
				setError(null);
			}
			let errors = {};
			return errors;
		},
	});

	const validate = (e) => {
		e.preventDefault();
		if (appliedCoupon === "") {
			const code = formic.values.code;
			if(code === "") {
				setError("please enter coupon code");
				setVerifiedCode(false);
				setTotalCost(originalCost);
				setDiscount(0);
				return;
			}
			CQAPI.verifyCoupon(code, props.token)
				.then((resp) => {
					setVerifiedCode(true);
					setDiscount(resp.discount);
					setTotalCost((originalCost - resp.discount).toString());
					setError(null);
					setAppliedCoupon(code);
				})
				.catch((err) => {
					let errorMessage = err.toString();
					if (errorMessage.substring(0, 7) === "Error: ") {
						errorMessage = errorMessage.substring(7);
					}
					setVerifiedCode(false);
					setError(errorMessage);
					setTotalCost(originalCost);
					setDiscount(0);
				});
		} else {
			setAppliedCoupon("");
			setVerifiedCode(false);
			setTotalCost(originalCost);
			setDiscount(0);
			formic.setValues({ code: "" });
		}
		// const [validated, cost, cred] = props.toValidate(formic.values.code)
	};
	return (
		<Form onSubmit={formic.handleSubmit}>
			<Container>
				<div className="checkoutCnt checkoutCnt2">
					<h3 className="createAccountHeading"> Payment Summary </h3>
					<div className="col-md-12">
						<div className="row">
							<div className="col-md-6 leftBdr text-center">
								<Form.Label className="font-weight-bold">
									Your Details
								</Form.Label>

								<ul className="list-unstyled">
									<li className="mb-4 text-secondary">
										{`${props.user.firstName} ${props.user.lastName}` ||
											"Sample"}
									</li>
									<li className="mb-4 text-secondary">
										{props.user.email[0]?.address ||
											"Email"}
									</li>
								</ul>
							</div>
							<div className="col-md-6 text-center">
								<Form.Label className="font-weight-bold">
									Your Report
								</Form.Label>

								<ul className="list-unstyled">
									<li className="mb-4 text-secondary">
										CQ Assessment:&nbsp;&nbsp;&#x20b9;
										{originalCost || "1"}
									</li>

									{verifiedCode && (
										<li className="mb-4 text-secondary">
											Discount:&nbsp;&nbsp;&#x20b9;
											{discount || "0"}
										</li>
									)}
									<li className="mb-4 text-secondary font-weight-bold">
										Order total:&nbsp;&nbsp;&#x20b9;
										{totalCost || "1"}
									</li>
								</ul>
							</div>
						</div>
					</div>
					<div className="couponCode">
						<strong>Coupon Code</strong>
						<Form.Group
							controlId="code"
							className="position-relative m_0"
						>
							{verifiedCode ? (
								<Alert variant="success">
									Coupon applied successfully!
								</Alert>
							) : null}
							<Row noGutters>
								<div className="col-md-9">
									<Form.Control
										value={formic.values.code}
										onChange={formic.handleChange}
										onBlur={formic.handleBlur}
										type="text"
										placeholder="Enter coupon code"
										disabled={appliedCoupon !== ""}
									/>
								</div>
								<div
									className="aplycode col-md-3"
									style={{ textAlign: "right" }}
								>
									<Link
										style={{ verticalAlign: "middle" }}
										onClick={validate}
									>
										{appliedCoupon !== ""
											? "CLEAR"
											: "APPLY"}{" "}
										CODE
									</Link>
								</div>
							</Row>
						</Form.Group>
						{error ? (
								<Alert
									variant="danger"
								>
									{error}
								</Alert>
							) : null}
						<div className="text-center wide-20">
							<Button
								type="submit"
								variant="primary loginBtn"
								disabled={formic.values.code && !verifiedCode}
							>
								{props.submitText || "Unlock Full Report"}
							</Button>
							{formic.values.code && !verifiedCode ? (
								<p className="text-muted">
									Apply valid coupon code or clear coupon code
									to continue
								</p>
							) : null}
						</div>
					</div>
				</div>
			</Container>
		</Form>
	);
};

export const LoginWithPassword = (props) => {
	const formic = useFormik({
		initialValues: {
			password: "",
		},
		onSubmit: (values, user) => {
			// console.log(values)
			props.onSubmit(values.password);
		},
		validate: (values) => {
			let errors = {};
			if (!values.password) {
				errors.password = "password required";
			}
			return errors;
		},
	});
	return (
		<div className="loginpage">
			<div className="loginCnt">
				<div className="col-md-12">
					<div className="row">
						<div className="col-md-6 displayNone">
							<div className="form-top">
								<img
									className="loginLogo"
									src="assets/CareerQuotient_large.jpeg"
									alt="Career Quotient Logo"
								></img>
							</div>
						</div>
						<div className="col-md-12">
							<div className="form-bottom">
								<h2 className="title loginText text-center">
									Password
								</h2>
								<Form
									className="loginForm passWordForm"
									onSubmit={formic.handleSubmit}
								>
									<div className="passwordCnt">
										<Form.Group controlId="password">
											<Form.Control
												value={formic.values.password}
												onChange={formic.handleChange}
												onBlur={formic.handleBlur}
												type="password"
												placeholder="Password"
												required
											/>
										</Form.Group>
										{props.passwordErrors ? (
											<Alert variant="danger errorMsg">
												{props.passwordErrors}
											</Alert>
										) : null}
										{formic.errors.password &&
										formic.touched.password ? (
											<Alert variant="danger errorMsg">
												{formic.errors.password}
											</Alert>
										) : null}

										<div className="pt-10 pb-10 text-right frgtLink">
											<Link
												onClick={
													props.handleForgotPassword
												}
												className="cq-color text-bold pull-right"
											>
												Forgot Password?
											</Link>
										</div>
									</div>

									{/* <Row>
				<Col className="text-right">
					<Button
						className="text-gold"
						size="sm"
						variant="link"
						style={{ paddingLeft: 0 }}
						onClick={props.onOTPRequest}
					>
						Reset Password
					</Button>
				</Col>
			</Row> */}
									<div className="mt-30 pb-30">
										<Button
											type="submit"
											variant="primary loginBtn"
										>
											Login
										</Button>
									</div>
								</Form>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export const ResetPasswordForm = (props) => {
	const [password, setPassword] = useState({
		value: "",
		error: {},
		isFocused: false,
	});

	const handlePasswordChange = (e) => {
		const value = e.target.value;
		setPassword({
			...password,
			value: value,
			error: {
				strLength: value.length >= 8,
				character: /[a-z A-Z]/.test(value),
				number: /[0-9]/.test(value),
			},
			isFocused: true,
		});
	};
	const formic = useFormik({
		initialValues: {
			password: "",
			confirmPassword: "",
			otp: "",
		},
		onSubmit: (values) => {
			values.password = password.value;
			if (Object.values(password.error).includes(false)) {
				setPassword({ ...password, isFocused: true });
				return;
			}
			props.handleResetPassword(values);
		},
		validate: (values) => {
			let errors = {};
			if (!values.otp) {
				errors.otp = "OTP required";
			}
			if (
				!values.confirmPassword ||
				values.confirmPassword !== password.value
			) {
				errors.confirmPassword = "Should be same as password above";
			}
			return errors;
		},
	});
	return (
		<div className="loginpage">
			<div className="loginCnt">
				<div className="col-md-12">
					<div className="row">
						<div className="col-md-6 displayNone">
							<div className="form-top">
								<img
									className="loginLogo"
									src="assets/CareerQuotient_large.jpeg"
									alt="Career Quotient Logo"
								></img>
							</div>
						</div>
						<div className="col-md-12">
							<div className="form-bottom">
								<h2 className="title loginText text-center">
									Reset Password
								</h2>
								<p>{props.forgotPasswordMessage}</p>
								<Form
									className="loginForm"
									onSubmit={formic.handleSubmit}
								>
									<div className="passwordCnt">
										<Form.Group controlId="otp">
											<Form.Control
												value={formic.values.otp}
												onChange={formic.handleChange}
												onBlur={formic.handleBlur}
												type="text"
												placeholder="OTP"
												required
											/>
											{formic.errors.otp &&
											formic.touched.otp ? (
												<Alert variant="danger errorMsg">
													{formic.errors.otp}
												</Alert>
											) : null}
										</Form.Group>
										<Form.Group controlId="password">
											<Form.Control
												value={password.value}
												onChange={handlePasswordChange}
												onBlur={formic.handleBlur}
												type="password"
												placeholder="Password"
												required
											/>
											{password.isFocused ? (
												<div className="validation">
													<div className="validator">
														<i
															className={
																password.error
																	.strLength
																	? "fas fa-check success error-icon"
																	: "fas fa-times error error-icon"
															}
														></i>
														<p
															className="validation-item"
															style={{
																margin:
																	"inherit",
																color: password
																	.error
																	.strLength
																	? "green"
																	: "red",
															}}
														>
															Minimum 8 characters
														</p>
													</div>
													<div className="validator">
														<i
															className={
																password.error
																	.number
																	? "fas fa-check success error-icon"
																	: "fas fa-times error error-icon"
															}
														></i>
														<p
															className="validation-item"
															style={{
																margin:
																	"inherit",
																color: password
																	.error
																	.number
																	? "green"
																	: "red",
															}}
														>
															At least one number
														</p>
													</div>
													<div className="validator">
														<i
															className={
																password.error
																	.character
																	? "fas fa-check success error-icon"
																	: "fas fa-times error error-icon"
															}
														></i>
														<p
															className="validation-item"
															style={{
																margin:
																	"inherit",
																color: password
																	.error
																	.character
																	? "green"
																	: "red",
															}}
														>
															At least one letter
														</p>
													</div>
												</div>
											) : (
												""
											)}
										</Form.Group>
										<Form.Group controlId="confirmPassword">
											<Form.Control
												value={
													formic.values
														.confirmPassword
												}
												onChange={formic.handleChange}
												onBlur={formic.handleBlur}
												type="password"
												placeholder="Confirm Password"
												required
											/>
											{formic.errors.confirmPassword &&
											formic.touched.confirmPassword ? (
												<Alert variant="danger errorMsg">
													{
														formic.errors
															.confirmPassword
													}
												</Alert>
											) : null}
										</Form.Group>
									</div>

									{/* <Row>
				<Col className="text-right">
					<Button
						className="text-gold"
						size="sm"
						variant="link"
						style={{ paddingLeft: 0 }}
						onClick={props.onOTPRequest}
					>
						Reset Password
					</Button>
				</Col>
			</Row> */}
									<div className="mt-30 pb-30">
										<Button
											type="submit"
											variant="primary loginBtn"
										>
											Reset Password
										</Button>
									</div>
								</Form>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};
