import React, { useEffect, useState } from "react";
import { Alert, Form, Button, Container, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { useFormik } from "formik";
import CQAPI from "../api";

const OTPForm = (props) => {
	const [timeLeft, setTimeLeft] = useState(30);
	const [canRequestOTP, setCanRequestOTP] = useState(false);
	useEffect(() => {
		const timer = setTimeout(() => {
			if (timeLeft < 1) {
				setCanRequestOTP(true);
				return;
			}
			setTimeLeft(timeLeft - 1);
		}, 1000);
		return () => clearTimeout(timer);
	});
	const formic = useFormik({
		initialValues: {
			otp: "",
		},
		onSubmit: (values, user) => {
			console.log(values)
			props.onSubmit(values.otp);
		},
		validate: (values) => {
			let errors = {};
			if (!values.otp) {
				errors.otp = "OTP required";
			}
			return errors;
		},
	});
	const resendOTP = () => {
		props.resendOTP();
		setTimeLeft(30);
		setCanRequestOTP(false);
	};
	return (
		<div class="loginpage">
	<div class="loginCnt">
		<div class="col-md-12">
			<div class="row">
				<div class="col-md-6 displayNone">
					<div class="form-top">
						<img class="loginLogo" src="assets/CareerQuotient_large.jpeg" alt="Career Quotient Logo"></img>
				</div>
				</div>
				<div class="col-md-12 otpCNT">
					<div class="form-bottom">
						
						{/* <form class="loginForm"  > */}
						<h2 class="title loginText text-center">OTP</h2>
						<p className="entrOtp">Enter the{" "}
					{`${
						props.loginMessage
							? props.loginMessage
							: "OTP sent to your registered mobile number"
					}`}</p>
						<div class="mt-20">
						<div class="form-group">
							<Form className="loginForm otpForm" onSubmit={formic.handleSubmit}>
			
			
			<Form.Group controlId="otp">
				
				<Form.Control
					value={formic.values.otp}
					onChange={formic.handleChange}
					onBlur={formic.handleBlur}
					type="text"
					placeholder="OTP"
					required
				/>
				<div className="mt-10">
				{formic.errors.otp && formic.touched.otp ? (
					<Alert variant="danger text-left ">{formic.errors.otp}</Alert>
				) : null}
				</div>
				<div className="userIDErro">
				{props.error ? <Alert variant="danger errorMsg">{props.error}</Alert>: null}
				</div>
				<div className="col-md-12 row">
				
				{ !canRequestOTP ?
					<div>
						<small className="text-muted otp_margin">You can request new OTP in&nbsp;
							{Math.floor(timeLeft/60).toLocaleString("en-US", {
								minimumIntegerDigits: 2,
							})}:
							{(timeLeft%60).toLocaleString("en-US", {
								minimumIntegerDigits: 2,
							})}
						</small>
					</div>: 
					<Button
						className="text-gold rqst_otp"
						size="sm"
						variant="link"
						disabled={!canRequestOTP}
						style={{ paddingLeft: 0 }}
						onClick={resendOTP}
					>
						Request new OTP
					</Button>}
					
			</div>
			</Form.Group>
			
			<div className="wide-40">
			<Button type="submit" variant="btn btn-primary loginBtn">
				{props.submitText}
			</Button>
			</div>
		</Form>
						</div>
						</div>					
						{/* </form> */}
					</div>
				</div>
			</div>
		</div>
	</div>
</div>
		
	);
};

function mapStateToProps(state) {
	return {
		loginMessage: state.auth.loginMessage,
	};
}

export default connect(mapStateToProps)(OTPForm);
