import { connect } from "react-redux";
import { Row, Col, Container, Alert } from "react-bootstrap";
import OTPForm from "../components/OTPForm";
import CQAPI from "../api";
import { useState } from "react";

const VerifyPhone = (props) => {
	const [error, setError] = useState();

	const resendOTP = () => {
		console.log(props.user);
		CQAPI.resendOTP(props.user.mobile.number).then((resp) => {
			console.log(resp);
		});
	};

	const handleLogin = (value) => {
		CQAPI.signIn({ userId: props.user.id, credentials: { otp: value } })
			.then((resp) => {
				props.onSuccess({
					user: resp.user,
					tokens: resp.tokens,
				});
				const params = new URLSearchParams(
					props.history.location.search
				);
				const redirect = params.get("redirect") || "home";
				console.log(redirect);
				props.history.push(`/${redirect}`);
			})
			.catch((err) => setError(err.toString()));
	};
	return (
		<div>
			<Row bsPrefix="fullscreen flex-center flex-column">
				<Col md={12}>
					{/* {error && <Alert variant="danger">{error}</Alert>} */}
					<OTPForm
						submitText="Login"
						onSubmit={handleLogin}
						resendOTP={resendOTP}
						error={error}
					/>
				</Col>
			</Row>
		</div>
	);
};

function mapStateToProps(state) {
	return { user: state.auth.user };
}

const mapDispatchToProps = (dispatch) => {
	return {
		onSuccess: (user) => dispatch({ type: "auth/setUser", payload: user }),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(VerifyPhone);
