import { CouponForm } from "../components/auth";
import { connect } from "react-redux";
import { Row, Col, Container, Alert } from "react-bootstrap";
import { Link } from "react-router-dom";
import React, { useState } from "react";
import CreateAccountForm from "../components/CreateAccountForm";
import CQAPI from "../api";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";
//
export const CreateAccount = (props) => {
	return (
		<div>
			<Row bsPrefix="fullscreen flex-center flex-column">
				<div className="checkoutCnt">
					<h3 className="createAccountHeading"> Create Account </h3>
					<CreateAccountForm {...props} />
				</div>
			</Row>
		</div>
	);
};

const CouponCheckout = (props) => {
	const [error, setError] = useState();
	const params = new URLSearchParams(props.history?.location?.search);
	const redirect = params.get("redirect") || "";
	const [fullRegistration, setFullRegistration] = useState(
		!props.user.fullyRegistered
	);
	const history = useHistory();

	function rzp_toggle(razorpayOptions) {
		console.log(razorpayOptions);
		razorpayOptions["handler"] = function (response) {
			console.log(response);
			CQAPI.verifyPayment(response.razorpay_payment_id)
				.then((resp) => {
					console.log(resp);
					props.onSuccess(resp);
				})
				.catch((err) => setError(err.message));
			history.push(`/report`);
		};
		const rzp = window.Razorpay(razorpayOptions);
		rzp.open();
		rzp.on("payment.failed", function (resp) {
			alert("Payment Failed Try Again");
		});
	}

	const validate = (code) => {
		CQAPI.verifyCoupon(code)
			.then((resp) => {
				return {
					validated: true,
					cost: resp.amount,
					cred: resp.razorpayOptions,
				};
			})
			.catch((err) => {
				setError(err.message);
			});
	};

	const handleSubmit = (code, token) => {
		// CQAPI.signUp(user).then((resp) => {
		//     props.onSuccess({ user: resp.user, tokens: resp.tokens })
		//     props.history.push(`/verify-phone?redirect=assessment-submit`)
		// }).catch((err) => setError(err.message))
		CQAPI.buyMembership(code, token)
			.then((resp) => {
				if (resp.membership.paymentInfo.success && resp.reportData) {
					props.onSuccess(resp);
					history.push(`/report`);
				} else {
					rzp_toggle(resp.razorpayOptions);
				}
			})
			.catch((err) => setError(err.message));
	};
	const handleFullRegistration = (data) => {
		console.log(props.user, props.token, data);
		CQAPI.fullRegistration(props.user.id, data, props.token)
			.then((resp) => {
				console.log(resp);
				setError(null);
				setFullRegistration(false);
			})
			.catch((err) => props.onError(err.message));
	};
	return (
		<Container fluid>
			<div className="fullscreen flex-center">
				<Col sm={12} md={8}>
					{error && (
						<Alert variant="danger mt-20 mb--20">{error}</Alert>
					)}
					{fullRegistration ? (
						<CreateAccount
							onSubmit={handleFullRegistration}
							user={props.user}
						/>
					) : (
						<CouponForm
							user={props.user}
							token={props.token}
							onSubmit={handleSubmit}
						/>
					)}
				</Col>
			</div>
		</Container>
	);
};

function mapStateToProps(state) {
	return {
		token: state.auth.tokens.access.token,
		user: state.auth.user,
		report: state.assessment.report,
	};
}

const mapDispatchToProps = (dispatch) => {
	return {
		onSuccess: (report) =>
			dispatch({ type: "assessment/reportResponse", payload: report }),
		onError: (error) =>
			dispatch({ type: "fullRegistration/error", payload: error }),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(CouponCheckout);
