import { Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { useState } from "react";

import Assessment from "./questions";
import { Login, SignUp, VerifyPhone } from "./auth";
import AssessmentReport from "./report";
import AssessmentResult from "./assessment_result";
import Home from "./homepage";
import { PageNotFound } from "./404";

const ProtectedRoute = ({ component: Component, ...rest }) => {
	const [protectedRedirectFrom, setprotectedRedirectFrom] = useState("");
	return (
		<Route {...rest}>
			{rest.user ? (
				<Component {...rest} />
			) : (
				<Redirect
					to={{
						pathname: rest.redirect,
						state: { from: rest.location },
					}}
				/>
			)}
			{console.log(rest)}
		</Route>
	);
};
function mapStateToProps(state) {
	return { user: state.auth.user };
}
export default connect(mapStateToProps)(ProtectedRoute);

export {
	Home,
	Login,
	SignUp,
	VerifyPhone,
	Assessment,
	AssessmentReport,
	AssessmentResult,
	PageNotFound,
};
